<template>
  <div class="getUserData">
    <el-form ref="form" label-width="120px">
    <el-form-item>
        <el-select v-model="userid" ref="selectUserEvent" placeholder="选择用户" @visible-change="selectUserEvent">
          <el-option
           v-if="user" v-for="item in user"
            :key="item.id"
            :label="item.user_name"
            :value="item.id">
          </el-option>
        </el-select>
          <el-button size="mini" @click="clear">重置清空</el-button>
      </el-form-item>
    </el-form>

    <el-dialog
      title="选择用户"
      :visible.sync="selectUserShow"
      :width="this.$store.state.modalWidth" append-to-body>
      <el-form ref="form" label-width="120px">
        <el-row :gutter="24">
          <el-col :sm="8">
            <el-input placeholder="请输用户名搜索" v-model="pager.user_name" class="input-with-select">
                <el-button slot="append" icon="el-icon-search" @click="query"></el-button>
            </el-input>
          </el-col>
          <el-col :span="8"></el-col>
        </el-row>
      </el-form>
      <br>

      <el-radio-group v-model="userid">
        <el-radio v-if="user" v-for="item in user" @change="radioChange(item)" :key="item.id" :label="item.id">{{item.user_name}}</el-radio>
      </el-radio-group>
      <br><br>
      <div class="paging-box" style="text-align:right;">
        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="pager.currentPage3"
            :page-size="pager.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pager.count">
          </el-pagination>
      </div>
      
      <span slot="footer" class="dialog-footer">
        <el-button @click="reset">重 置</el-button>
        <el-button @click="close">取 消</el-button>
        <el-button type="primary" @click="setUser">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['getUserIdFn'],
  data () {
    return {
      selectUserShow: false,
      userid: 0,
      pager: {
        currentPage3: 1,
        count: 0,
        pageSize: 100,
        size: 0,
        user_name: ''
      },
      user: null,
      userItem: {id:0}
    }
  },
  created () {
    this.getUserData()
  },
  methods: {
    selectUser () {
        this.selectUserShow = true
      },
    clear () {
      this.userid = 0
      this.user = null
    },
    selectUserEvent (val) {
      if (val) {
        this.selectUser()
      }
      this.$refs.selectUserEvent.blur()
    },
    query () {
      this.pager.size = 0
      this.pager.pageSize = 100
      this.pager.count = 0
      this.getUserData()
    },
    handleSizeChange (val) {
      this.pager.pageSize = val
      this.getUserData()
    },
    handleCurrentChange (val) {
      this.pager.size = val - 1
      this.getUserData()
    },
    reset () {
      this.userItem = {id: 0}
      this.userid = 0
    },
    radioChange (item) {
      this.userItem = item
    },
    setUser () {
      this.getUserIdFn(this.userItem)
      this.selectUserShow = false
    },
    close () {
      this.selectUserShow = false
    },
    getUserData () {
        var _this = this
      _this.$get({
        type: '$get',
        obj: {
          url: _this.api.getuser,
          success: function (res) {
            if (res.status == 200) {
              _this.user = res.data.user
              _this.pager.count = res.data.count
            }
            else {
              _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
            }

          },
          error: function () {
            _this.alerter('获取菜单数据失败！','获取菜单数据',2000)
          }
        },
        param: {
          size: _this.pager.size,
          sizes: _this.pager.pageSize,
          search: _this.pager.user_name
        }
      })
    },
  }
}
</script>
<style>
.user .el-input__icon{
  line-height: inherit;
}
.paging-box{
  padding-top:20px;text-align:right;
}
.getUserData .el-radio{
  margin-left:0px;margin-bottom:20px;width:100px;
}
</style>



